import { useState, useEffect, useRef } from 'react'

export const useGeolocation = () => {
  const [state, setState] = useState({
    accuracy: null,
    altitude: null,
    altitudeAccuracy: null,
    heading: null,
    latitude: null,
    longitude: null,
    speed: null,
    timestamp: Date.now()
  })
  const mountedRef = useRef()
  const watchIdRef = useRef()
  mountedRef.current = true

  const onEvent = event => {
    if (mountedRef.current) {
      setState({
        accuracy: event.coords.accuracy,
        altitude: event.coords.altitude,
        altitudeAccuracy: event.coords.altitudeAccuracy,
        heading: event.coords.heading,
        latitude: event.coords.latitude,
        longitude: event.coords.longitude,
        speed: event.coords.speed,
        timestamp: event.timestamp
      })
    }
  }

  useEffect(() => {
    navigator.geolocation.getCurrentPosition(onEvent)
    watchIdRef.current = navigator.geolocation.watchPosition(onEvent)

    return () => {
      mountedRef.current = false
      navigator.geolocation.clearWatch(watchIdRef.current)
    }
  }, [navigator.geolocation])

  return state
}

export function useDeviceOrientation () {
  const [deviceOrientation, setDeviceOrientation] = useState({ absolute: false, alpha: null, beta: null, gamma: null })

  function handleDeviceOrientation (event) {
    setDeviceOrientation({
      absolute: event.absolute,
      alpha: event.alpha,
      beta: event.beta,
      gamma: event.gamma
    })
  }

  useEffect(() => {
    window.addEventListener('deviceorientation', handleDeviceOrientation, true)

    return () => {
      window.removeEventListener('deviceorientation', handleDeviceOrientation)
    }
  }, [])

  return deviceOrientation
}

export function playClickAudio (clickSound) {
  let audio = new Audio('data:audio/mp3;base64,' + clickSound.base64)
  audio.play()
}
