import React from 'react'
import PropTypes from 'prop-types'
import List from '@material-ui/core/List'
import Typography from '@material-ui/core/Typography'

import SearchContextItem from './SearchContextItem'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles } from '@material-ui/core/styles'
import SeriesListItem from '../misc/SeriesListItem'

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%'
  },
  list: {
    width: '100%',
    height: '100%',
    backgroundColor: theme.palette.background.paper,
    position: 'relative',
    overflowX: 'hidden',
    overflowY: 'scroll'
  },
  placeholderText: {
    textAlign: 'center',
    padding: theme.spacing(3, 1),
    color: theme.palette.grey[400]
  },
  listSection: {
    backgroundColor: 'inherit'
  },
  ul: {
    backgroundColor: 'inherit',
    padding: 0
  }
}))

const SearchResults = ({ variant, loading, seriesList, contextChoice, onCancel, onResultClick }) => {
  const classes = useStyles()

  let content = null
  if (loading) {
    content = (
      <Typography className={classes.placeholderText} variant='h3'>Searching...</Typography>
    )
  }
  else if (seriesList && seriesList.length > 0) {
    content = seriesList.map((series, idx) => (
      <SeriesListItem
        key={`series-${idx}`}
        series={series}
        onClick={() => onResultClick(series.id)}
        showCreatedOn
        showLastPhoto
      />
    ))
  }
  else {
    content = (
      <Typography className={classes.placeholderText} variant='h4'>No series found</Typography>
    )
  }

  return (
    <div className={classes.root}>
      <SearchContextItem {...contextChoice} variant={variant} onCancel={onCancel} />
      <List className={classes.list} subheader={<li />}>
        {content}
      </List>
    </div>
  )
}

SearchResults.propTypes = {
  variant: PropTypes.oneOf(['tag', 'location', 'user']),
  seriesList: PropTypes.array.isRequired,
  loading: PropTypes.bool,
  contextChoice: PropTypes.object.isRequired,
  onResultClick: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired
}

export default SearchResults
